.map{
  &__list{
    margin-top: 50px;
    margin-bottom: 20px;
    // padding: 10px;
    box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
    background: #272937;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90%;
    &-item{
      padding: 15px;
      margin-right: 15px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      &.active::before{
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        background: #2196f3;
        border-radius: 2px;
        border: 2px solid #36384a;
        left: -5px;
      }
      &:last-child{
        margin-right: 0px;
      }
      &::before{
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        background: #222431;
        border-radius: 2px;
        border: 1px solid #36384a;
        left: -5px;
        // top: 20px;
      }
    }
    &-btn{
      color: white;
    }
  }
  &__img{
    &-wrapper{
      width: 90%;
    }
  }
}