.character{
  &__list{
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    margin-top: 30px;
    // padding-left: 11px;
    
    width: 97%;
    flex-wrap: wrap;
    // margin-right: 0px;
    align-items: center;
    justify-content: center;
  }
  &__item{
    width: 60px;
    height: 60px;
    position: relative;
    display: flex;
    margin-bottom: 50px;
    margin-right: 5px;
    margin-left: 5px;
    margin-top: 10px;
    border-radius: 10px;
    background-color: #36384a;
  }
  &__name{
    position: absolute;
    bottom:-25px;

    color: white;
  }
  &__link{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-size: cover;
    &.ganyu{
      background-image: url(./character/Ganyu.png);
    }
    &.amber{
      background-image: url(./character/Amber.png);
    }
    &.barbara{
      background-image: url(./character/Barbara.png);
    }
    &.beidou{
      background-image: url(./character/Beidou.png);
    }
    &.bennet{
      background-image: url(./character/Bennett.png);
    }
    &.chongyun{
