.character__link.cryo:after{
  content: '';
  width: 25px;
  height: 25px;
  position: absolute;
  z-index: 2;
  top: -15px;
  background-image: url(./../ToolFilter/elements/Element_Cryo.png);
  background-size: cover;
}
