@media (min-width: 830px){
  .character__link.cryo:after{
    width: 35px;
    height: 35px;
  }
  .character{
    &__item{
      width: 70px;
      height: 70px;
      margin: 20px;
      margin-top: 30px;
    }
    &__list{
      margin-top: 10px;
    }
    &__link{
      filter: opacity(70%);
      &:hover{
        filter: opacity(100%);
      }
    }
  }
}

