.main{
  margin-top: 30px;
  background-color: #222431;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
  padding-bottom: 30px;
  box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
  &__title{
    display: flex;
    flex-direction: column;
    width: 90%;
    position: relative;
    margin: 0 auto;
    &-btn{
      background-color: #222431;
    }
    &-close{
      width: 21px;
      height: 21px;
      position: relative;
      &::after{
        content: "";
        background-color: white;
        position: absolute;
        width: 21px;
        height: 2px;
        left: -3px;
        top: 9px;
        transform: rotate(45deg);
      }
      &::before{
        content: "";
        background-color: white;
        position: absolute;
        width: 21px;
        height: 2px;
        left: -3px;
        top: 9px;
        transform: rotate(-45deg);
      }
    }
    &-form{
      display: flex;
      align-items: center;
      width: 70%;
      height: 100%;
      justify-content: space-between;
    }
    &-input{
      width: 100%;
      display: flex;
      border: none;
      height: 30px;
      background-color: #222431;
    }
    &-text{
      color: white;
      // margin-bottom: 20px;
      padding: 10px;
      font-size: 20px;
    }

    &-btn{
      background-image: url(./search.svg);
      background-size: cover;
      margin-left: 10px;
      margin-right: 15px;
      width: 21px;
      height: 21px;
    }
  }
}

.search-box{
  background-color: #222431;
  border-radius: 3px;
  border: 1px solid #1d1f29;
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: space-between;
  &:hover{
    border: 1px solid #007bff;
  }
}

.nope{
  display: none;
}

@media (min-width: 830px) {
  .main{
    margin-top: 100px;
  }
  .main__title{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    &-text{
      margin-top: 0px;
      font-size: 25px;
    }
    &-input{
    }
    .search-box{
      width: 300px;
    }
  }
}