.tearlist{
  &__list{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &-character{
    margin-top: 0px;
    align-self: center;
    margin: 0 auto;
    padding: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  &__listing{
    width: 90%;
  }
  &__wrapper{
    display: flex;
    flex-direction: row;
    background-color: #272937;
    width: 100%;
    min-height: 130px;
    margin-top: 30px;
    box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
  }
  &__rank{
    min-width: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    &-name{
      font-size: 30px;
    }
    &-s{
      background-color: #ff7f7f;
    }
    &-b{
      background-color: #ffff7f;
    }
    &-c{
      background-color: #bfff7f;
    }
    &-d{
      background-color: #7fff7f;
    }
    &-a{
      background-color: #ffbf7f;;
    }
  }
}