.footer{
  background-color: #222431;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
  &__logotype{
    margin-left: 0px;
  }
  &__r{
    display: flex;
    flex-direction: column;
  }
  &__l{
    max-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__listing{
    &-name{
      color: white;
      font-size: 20px;
    }
  }
  &__list{
    margin-bottom: 30px;
  }
  &__text{
    margin-top: 10px;
    margin-bottom: 30px;
    color: white;
    padding-left: 10px;
    padding-right: 10px;
  }
  &__link{
    color: white;
    text-decoration: none;
    font-size: 20px;
    &:hover{
      color: white;
    }
  }
}

@media (min-width: 830px){
  .footer{
    background-color: #222431;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  
    &__r{
      display: flex;
      flex-direction: row;
    }
    &__l{
      max-width: 400px;
      display: flex;
      flex-direction: column;
    }
    &__listing{
      &-name{
        color: white;
        font-size: 20px;
      }
    }
    &__list{
      margin-right: 45px;
    }
    &__link{
      color: gray;
      text-decoration: none;
      font-size: 20px;
      &:hover{
        color: white;
      }
    }
  }
}