.ava{
  display: flex;
  width: 100%;
  height: 150px;
  background-image: url(./character-background.png);
  background-size: cover;
  
  background-size: cover;
  &__head{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    background-color: rgba(0, 120, 201, 0.2);
    padding-left: 30px;
    padding-right: 30px;
    &-left{
      display: flex;
      align-items: center;
    }
    &-name{
      display: flex;
      flex-direction: column;
      color: white;
      font-weight: 600;
      margin-left: 30px;
    }
    &-char{
      width: 80px;
      height: 80px;
      background-size: cover;
    }
    &-ganyu{
      background-image: url(./../../../src/Main/character/Ganyu.png);
    }
    &-rank{
      width: 50px;
      height: 50px;
      background-color: #ff7f7f;;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 7px;
      font-size: 30px;
    }
  }
}

.char{
  width: 100%;
  background-color: #222431;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  &__skills{
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 5px;
    margin-top: 10px;
    &-desc{
      color: #a7b1c1;
      margin-bottom:20px;
      font-size: 13px;
    }
    &-atribute{
      color: #a7b1c1;
      font-weight: 500;
      font-size: 15px;
    }
    &-name{
      color: #ffd780;
      font-weight: 600;
      font-size: 18px;
      margin-bottom: 10px;
    }
    &-names{
      margin-top: 20px;
      margin-bottom: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &-na{
      display: flex;
      flex-direction: column;
      width: 70%;
      margin-top: 10px;
      padding: 10px;
      background-color: #272937;
      box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
    }
    &--op{
      margin-bottom: 30px;
      display: flex;
      flex-direction: column;
    }
  }
}

