.tools{
  padding: 10px;
  display: flex;
  flex-direction: column;
  margin-top: 30px !important;
  width: 90%;
  margin: 0 auto;
  justify-content: space-between;
  background-color: rgba(black, 0.4);
  border-radius: 10px;
  min-height: 50px;
  &__list{
    display: flex;
    justify-content: space-around;
    &-item{
      padding: 3px;
      display: flex;
    }
    &-btn{
      width: 30px;
      height: 30px;
      // border-radius: 5px;
      // background-color: white;
      background-size: cover;
      filter: opacity(50%);
      &.anemo{
        background-image: url(./elements/Element_Anemo.png);
      }
      &.cryo{
        background-image: url(./elements/Element_Cryo.png);
      }
      &.electro{
        background-image: url(./elements/Element_Electro.png);
      }
      &.dendro{
        background-image: url(./elements/Element_Dendro.png);
      }
      &.geo{
        background-image: url(./elements/Element_Geo.png);
      }
      &.hydro{
        background-image: url(./elements/Element_Hydro.png);
      }
      &.pyro{
        background-image: url(./elements/Element_Pyro.png);
      }
      &.bow{
        background-image: url(./weapons/Bow.png);
      }
      &.boock{
        background-image: url(./weapons/boock.png);
      }
      &.bigsword{
        background-image: url(./weapons/BigSword.png);
      }
      &.spear{
        background-image: url(./weapons/spear.png);
      }
      &.sword{
        background-image: url(./weapons/sword.png);
      }
    }
  }
}
.tools__list-item.active{
  border-radius: 10px;
  background-color: #36384a;
}

@media (min-width: 830px){
  .tools{
    display: flex;
    flex-direction: row !important;
    &__list{
      display: flex;
      width: 40%;
      flex-direction: row;
      margin-top: 5px;
      &-item{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        &:hover{
          border-radius: 10px;
          background-color: #36384a;
        }
      }
      &-btn{
        width: 30px;
        height: 30px;
      }
      &-btn:hover{
        filter: opacity(100%);
      }
    }
  }
}